import React, { Component, useState, useEffect } from 'react';
import PropTypes from 'prop-types';
import Card from '@material-ui/core/Card';
import CardContent from '@material-ui/core/CardContent';
import Typography from '@material-ui/core/Typography';
import Container from "@material-ui/core/Container";
import Grid from '@material-ui/core/Grid';

import Fab from '@material-ui/core/Fab';
import CachedIcon from '@material-ui/icons/Cached';

import axios from 'axios';

// const datas = [
//   { main: "menu1", sub1: "sub1 1", sub2: "sub2 1", sub3: "sub3 1", date: 1 },
//   { main: "menu2", sub1: "sub1 2", sub2: "sub2 2", sub3: "sub3 2", date: 2 },
//   { main: "menu3", sub1: "sub1 3", sub2: "sub2 3", sub3: "sub3 3", date: 3 },
//   { main: "menu4", sub1: "sub1 4", sub2: "sub2 4", sub3: "sub3 4", date: 4 }
// ]

const MyCard = (props) => {
  const data = props.data;
  return (
    <Card>
      <CardContent>
        <Typography variant="h5" component="h2">
          {data.main}
        </Typography>
        <Typography color="textSecondary">
          {data.sub1}
        </Typography>
        <Typography color="textSecondary">
          {data.sub2}
        </Typography>
        <Typography color="textSecondary">
          {data.sub3}
        </Typography>
      </CardContent>
    </Card>
  );
}

MyCard.propTypes = {
  data: PropTypes.object.isRequired
};

const RandList = (props) => {
  let [users, setUsers] = useState([]);
  useEffect(async() => {
    const response = await axios("https://raw.githubusercontent.com/Yang-33/MyGitHubPages/master/Gohan/homemenulist.json",);
    //const response = await axios("http://localhost:3000/Gohan/homemenulist.json",);
    setUsers(response.data);
  }, []);

  const selected_datas = [];
  const data_n = users.length;
  if (users.length) {
    props.rands.map(elem => {
      selected_datas.push(users[elem % data_n]);
    });
  }

  const RandListsyle = {
    marginTop: "1px"
  };

  const cards = (
    <Grid container spacing={4} style={RandListsyle}>
      {selected_datas.map((item, idx) => {
        return (
          <Grid item xs={12} key={idx}>
            <MyCard data={item} key={idx} />
          </Grid>
        );
      })}
    </Grid>
  );
  return (
    cards
  );
};

RandList.propTypes = {
  rands: PropTypes.array.isRequired
};

const randgen = () => {
  return Math.floor(Math.random() * Math.floor(365));
}

class RandomMenu extends Component {
  constructor(props) {
    super(props);
    this.state = {
      rands: [randgen(), randgen(), randgen()]
    }
    this.handleClickButton = this.handleClickButton.bind(this);
  }

  handleClickButton() {
    const new_rands = [randgen(), randgen(), randgen()];
    this.setState(
      { rands: new_rands }
    );
  }

  render() {
    const Fabstyle = {
      position: 'absolute',
      bottom: "70px",
      right: "20px",
    };

    return (
      <Container>
        <RandList rands={this.state.rands}/>
        <Fab color="primary" aria-label="add" onClick={this.handleClickButton} style={Fabstyle}>
          <CachedIcon />
        </Fab>
      </Container>
    )
  }
}

export default RandomMenu;
